// components / dashboard / footer

// Modules
import React from "react";
import {connect} from 'react-redux';
import io from 'socket.io-client';

// Config
import config from '../../../config';

// Action
import {clearCurrentMessage, replyMessage, replyMessageSocket} from '../../../action/MessageAction.js';


let socket = io(config.serverURL + "/message", {secure: true});

class ChatMessage extends React.Component {
    componentDidMount() {
        // connection for socket.io
        socket.emit('join', {
            userUUID: this.props.user.uuid,
            msgUUID: this.props.currentMessage.uuid
        });

        socket.on('updateMsg', (data) => {
            this.props.replyMessageSocket(data);
            this.updateMsgChatScroll();
        });

        this.updateMsgChatScroll();
    }

    componentWillUnmount() {
        this.props.clearCurrentMessage();
        socket.emit('leave', {
            userUUID: this.props.user.uuid,
            msgUUID: this.props.currentMessage.uuid
        });
        socket.removeListener('updateMsg');
    }

    updateMsgChatScroll() {
        // always set chat display to the latest (bottom)
        // let contentChatBoxChats = document.getElementsByClassName("chat-activity-list")[0];
        let contentChatBoxChats = document.getElementsByClassName("chat-activity-list")[0];
        contentChatBoxChats.scrollTop = contentChatBoxChats.scrollHeight;
    }

    handleSubmit(e) {
        e.preventDefault();
        let arg = {
            userUUID: this.props.user.uuid,
            userFullname: this.props.user.first_name + ' ' + this.props.user.last_name,
            msgUUID: this.props.currentMessage.uuid,
            msgCreatedAt: (new Date()).toISOString(),
            content: this.refs.content.value
        };
        socket.emit('replyMsg', arg);
        this.refs.content.value = '';
    }

    render() {
        // check message type
        let messageType = "label-success";
        switch (this.props.currentMessage.type) {
            case "message":
                messageType = "label-success";
                break;

            case "complaint":
                messageType = "label-danger";
                break;

            case "suggestion":
                messageType = "label-info";
                break;

            case "security":
                messageType = "label-warning";
                break;
        }


        let chatting = this.props.currentMessage.chats.map((chat, index) => {
            let myMessage = chat.sender_uuid !== this.props.currentMessage.recipient.uuid;

            return (
                <div key={index} className={"chat-element " + (myMessage ? 'right' : '' )}>
                    <a href="#" className={"pull-" + (myMessage ? 'right' : 'left' )}>
                        <img alt="image" className="img-circle" src={myMessage ? "http://webapplayers.com/inspinia_admin-v2.7.1/img/a4.jpg" : "http://webapplayers.com/inspinia_admin-v2.7.1/img/a2.jpg"} />
                    </a>
                    <div className={"media-body " + (myMessage ? 'text-right' : '' )}>
                        <small className={"pull-" + (myMessage ? 'left' : 'right' )}>
                            {moment(chat.created_at).fromNow()}
                        </small>
                        <strong>
                          {myMessage ? 'You' : chat.sender}
                        </strong>
                        <p className="m-b-xs">
                            {chat.content}
                        </p>
                        <small className="text-muted">
                            {moment(chat.created_at).format('h.mm a - DD.MM.YYYY')}
                        </small>
                    </div>
                </div>
            );
        });


        return (
            <div className="col-lg-9 animated fadeInRight">
                <div className="mail-box-header">
                    <div className="pull-right tooltip-demo">
                        <a href="#" className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Reply"><i className="fa fa-reply"></i> Reply</a>
                        <a href="#" className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Print email"><i className="fa fa-print"></i> </a>
                        <a href="#" className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Move to trash"><i className="fa fa-trash-o"></i> </a>
                    </div>
                    <h2>
                        Current Message
                    </h2>
                    <div className="mail-tools tooltip-demo m-t-md">


                        <h3>
                            <span className="font-normal">Subject: </span> {this.props.currentMessage.subject}
                        </h3>
                        <h5 style={{textTransform: 'capitalize'}}>
                            <span className="font-normal">Type: </span> <span className={"label " + messageType}>{this.props.currentMessage.type}</span>
                        </h5>
                        <h5>
                            <span className="pull-right font-normal">{moment(this.props.currentMessage.created_at).format('DD MMMM YYYY, h.mm a')}</span>
                            <span className="font-normal">Conversation with: </span> {this.props.currentMessage.recipient.name} - ({this.props.currentMessage.recipient.type})
                        </h5>
                    </div>
                </div>
                <div className="mail-box">
                    {/*
                    <div className="mail-body">
                        <p>
                            {this.props.currentMessage.chats[0].content}
                        </p>

                        <p>
                            From: {this.props.currentMessage.chats[0].sender !== this.props.currentMessage.recipient.name ? 'Myself' : this.props.currentMessage.chats[0].sender }
                        </p>
                    </div>
                    */}

                    <div className="mail-attachment">
                        <div className="col-lg-offset-1 col-lg-10">
                            <div className="marking-chat ibox float-e-margins">
                                    <div className="ibox-content">
                                        <div>
                                            <div className="chat-activity-list">
                                                {chatting}
                                            </div>
                                        </div>
                                        <div className="chat-form">
                                            <form onSubmit={this.handleSubmit.bind(this)}>
                                                <div className="form-group">
                                                    <textarea className="form-control" rows="4" placeholder="Message" ref="content"></textarea>
                                                </div>
                                                <div className="text-right">
                                                    <button type="submit" className="btn btn-sm btn-primary m-t-n-xs"><strong>Send message</strong></button>
                                                </div>
                                            </form>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div className="clearfix"></div>



                    <div className="mail-body text-right tooltip-demo">
                            <a className="btn btn-sm btn-white" href="#"><i className="fa fa-reply"></i> Reply</a>
                            <a className="btn btn-sm btn-white" href="#"><i className="fa fa-arrow-right"></i> Forward</a>
                            <button title="" data-placement="top" data-toggle="tooltip" type="button" data-original-title="Print" className="btn btn-sm btn-white"><i className="fa fa-print"></i> Print</button>
                            <button title="" data-placement="top" data-toggle="tooltip" data-original-title="Trash" className="btn btn-sm btn-white"><i className="fa fa-trash-o"></i> Remove</button>
                    </div>
                    <div className="clearfix"></div>
                  </div>
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user, messages: state.messages.messages, currentMessage: state.messages.currentMessage};
}

const mapDispatchToProps = (dispatch) => {
    return {clearCurrentMessage, replyMessage, replyMessageSocket};
}

export default connect(mapStateToProps, mapDispatchToProps())(ChatMessage);



