// pages / dashboard / conversation / message

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import {navigateTo} from "gatsby-link";

// Styles
import styles from '../../../style/preloader.module.css';

// Components
import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class ConversationMessage extends React.Component {
    componentDidMount() {
        // eject user if user try to enter url manually
        // if message aray is empty initially, eject user
        if (this.props.messages.length === 0) {
            navigateTo("/dashboard/conversation");
        }
    }

    render() {
        if (this.props.user.loggedIn && this.props.messages.length !== 0) {

            return (
                <div className="wrapper wrapper-content">

                    {/* Start of Helmet */}
                    <Helmet title="Conversation - message" meta={[
                        {
                            name: 'description',
                            content: 'Conversation - message'
                        }, {
                            name: 'keywords',
                            content: 'Conversation'
                        }
                    ]}/>
                    {/* End of Helmet */}



                    {
                        this.props.currentMessage.type !== null ?
                        (
                            /* If there is message, then show it to users */
                            <div className="row">
                                <ConversationSidebar messagesLength={this.props.messages.length}/>
                                <ChatMessage />
                            </div>
                        ) :
                        (
                            /* If there is no message yet, show loading preloader */
                            <div className={styles.preloader1 + ' ' + styles.active }>
                                <i className={styles.preloaderIcon + " fa fa-refresh"}></i>
                            </div>
                        )
                    }
                </div>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user, messages: state.messages.messages, currentMessage: state.messages.currentMessage};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(ConversationMessage);



